
import { Personage, Saveuser } from '@/api/index'
import dateFormat from 'vux/src/tools/date/format.js'

export default {
data () {
return {
layout:false,
user:[],
bookss: [],
books:[],
values:'',
Blength:0,
pushCount:[],
Number: true,
delay:true,
mask:false,
newbook:[],
text1:false,
text2:false,
text3:false
}
},
methods: {
textM() {
window.scroll(0,0)
var myreg= /^[1][3,4,5,6,7,8,9][0-9]{9}$/
if( myreg.test(this.user.phone)) {
this.$refs.Moblie.style.color = '#000'
this.Number = true
} else {
this.$refs.Moblie.style.color = 'red'
this.Number = false
this.layout = true
this.verify()
}
},
verify() {
setTimeout(() => {
this.layout = false
}, 1500);
},
bblur() {
window.scroll(0,0);
},
cardDel(i) {
this.books.splice(i,1)
},
cardBook () {
this.books.push({})
},
record() {
this.pass = true
if (!this.user.phone && !this.Number ) {
this.pass = false
this.text1 = true
return setTimeout(() => {
this.text1 = false
}, 1500);
}
if(!this.user.name) {
this.pass = false
this.text2 = true
return setTimeout(() => {
this.text2 = false
}, 1500);
}
this.books.forEach( el => {
var arr = Object.keys(el)
if(  arr.length != 2 ) {
this.pass = false;
this.text3 = true
return setTimeout(() => {
this.text3 = false
}, 1500);
}
});
this.delay = true
if( this.pass && this.delay ) {
this.delay = false
this.pushCount.volunteer = this.values
this.pushCount.bonaStart = this.pushCount.bonaStart.replace('年','-')
this.pushCount.bonaStart = this.pushCount.bonaStart.replace('月','-')
this.pushCount.bonaStart = this.pushCount.bonaStart.replace('日','')
this.newbook = this.bookss.concat(this.books)
delete this.pushCount.bonaTotal
Saveuser({books:this.newbook,user:this.user,pushCount:this.pushCount}).then( res => {
if(res.code ==1) {

localStorage.setItem('userName',this.user.name)
this.delay = true
this.mask = true
this.gainUser()
return  setTimeout(() => {
this.mask = false
}, 1500);
}
})
}

},
gainUser() {
Personage({}).then( res => {
if(res.code == 1 ) {
this.user = res.data.user
this.bookss = res.data.books
this.Blength = res.data.books.length + 1
this.pushCount = res.data.pushCount
this.values = res.data.volunteer
}
})
}
},
created () {
this.gainUser()
},
components: {
}
}



