import { Parise } from '@/api/index'
export default {
  name: 'clock', // 结构名称
  data() {
    return {
      // 数据模型
      userid:localStorage.getItem('userid'),

    }
  },
  props: {
    // 集成父级参数
    clocklist:{
      type:Array
    },
  },
  methods: {
    //跳转链接
    jumpLink(link){
      window.location.href=link
    },
    //跳转详情
    jumpDetail(id,userid){
      this.$router.push({
        path:'/cardDetail',
        query:{
          id:id,
          userid:userid
        }
      })
    },
    //打卡圈
    jumpFriend(id){
      this.$router.push({
        path:'/nav/friend',
        query:{
          id:id
        }
      })
    },
    // 方法 集合
    fullClick(i){
      let obj=this.clocklist[i];
      if(obj.fullTxt=='收起'){
        obj.isFull=false;
        obj.fullTxt='全文';
      }else{
        obj.isFull=true;
        obj.fullTxt='收起';
      }
      this.$set(this.clocklist,i,obj);
    },
    isPariseFn(i){
      let obj=this.clocklist[i];
      obj.isParise=true;
      // if(this.name==''){
      //   return false
      // }
      let id=this.clocklist[i].pkid;
      let that=this;
      Parise({logId:id}).then(res =>{
        if(res.msg=='操作成功'){
          if(that.strInArr(obj.praise,res.data)){
            obj.praise.push(res.data);
          }
          that.$set(that.clocklist,i,obj);
        }else{
          that.$toast('因网络原因，点赞失败')
          obj.isParise=false;
        }
      })
    },
    strInArr(arr,str){
      if(arr.length==0){
        return true
      }
      for(let x of arr){
        if(x==str){
          return false
        }else{
          return true
        }
      }
    },
  }

}




